import React, { Component } from 'react'
import {format, parse} from 'date-fns'
import './AthleteDetailCard.scss'

class AthleteDetailCard extends Component {
  render() {
    const { selectedResult, onClick } = this.props
    if (selectedResult === null) {
      return <div className="athlete-detail__card media rounded mb-3 p-3 pb-0"></div>
    }
    const formattedDate = format(parse(selectedResult.d_PhaseDate), 'D MMMM YYYY')
    const name = selectedResult.c_Person === null ? `Team ${selectedResult.c_Team}` : selectedResult.c_Person
    let medal, medalCssClass
    const result = selectedResult.c_Result !== null ? (<span>Result ({selectedResult.c_ResultType}): {selectedResult.c_Result}</span>) : null
    switch (selectedResult.n_RankMedal) {
      case 1:
        medalCssClass = 'medal--gold'
        medal = ''
        break
      case 2:
        medalCssClass = 'medal--silver'
        medal = ''
        break
      case 3:
        medalCssClass = 'medal--bronze'
        medal = ''
        break
      default:
        break
    }

    const cssClass = `athlete-detail__card media rounded mb-3 p-3 pb-0 ${medalCssClass}`
    return (
      <div className={cssClass} onClick={e => {e.preventDefault();onClick();}}>
        <div className="media-body text-center">
          <h1 className="my-3 d-flex align-items-center"><i className="material-icons material-icons--large">navigate_before</i> {name}</h1>
          <p className="display-2">{medal}</p>
          <h3>{selectedResult.c_EventGender} { selectedResult.c_Sportname}</h3>
          <h4 className="text-secondary">{selectedResult.c_CompetitionName}</h4>
          <hr/>
          <p className="text-muted">
            {formattedDate} {selectedResult.n_PersonAgeYears ? `(${selectedResult.n_PersonAgeYears} years old)` : ''} <br/>
            {selectedResult.c_PhaseCity}, {selectedResult.c_PhaseCountry} <br/>
            {result}
          </p>
        </div>
      </div>
    )
  }
}

export default AthleteDetailCard