import { SELECT_RESULT, UNSET_POI, UNSELECT_RESULT } from '../constants/ActionTypes'

const initialState = {}

const selectedResult = (state = initialState, action) => {
  switch (action.type) {
    case SELECT_RESULT: {
      return action.selectedResult
    }
    case UNSET_POI: {
      return null
    }
    case UNSELECT_RESULT: {
      return null
    }
    default:
      return state
  }
}

export default selectedResult
