import React, { Component } from 'react'
import {format, parse} from 'date-fns'
import './AthleteCard.scss'

class AthleteCard extends Component {
  render() {
    const { result, onClick, isActive } = this.props
    const formattedDate = format(parse(result.d_PhaseDate), 'D MMMM YYYY')
    const name = result.c_Person === null ? `Team ${result.c_Team}` : result.c_Person
    let medalCssClass = ''
    let medal = ''
    switch (result.n_RankMedal) {
      case 1:
        medalCssClass = 'medal--gold'
        medal = ''
        break
      case 2:
        medalCssClass = 'medal--silver'
        medal = ''
        break
      case 3:
        medalCssClass = 'medal--bronze'
        medal = ''
        break
      default:
        break
    }
    // const medal = result.n_RankMedal === 1 ? "" : (result.n_RankMedal === 2 ? "" : (result.n_RankMedal === 3 ? "" : result.n_RankMedal))
    const cssClass = `athlete-card media border border-dark rounded mb-3 p-3 pb-0 ${medalCssClass} ${isActive ? 'athlete-card--active' : ''}`
    return (
      <div className={cssClass} onClick={e => {e.preventDefault();onClick();}}>
        <div className="media-body">
          <h5 className="mt-0">{medal} {name}</h5>
          <p className="mb-0 text-muted">
            {result.c_EventGender} { result.c_Sportname}, {result.c_CompetitionName}<br/>
            <small className="text-muted">
              {result.c_PhaseCity} {result.c_PhaseCountry}<br/>
              {formattedDate}
            </small>
          </p>
        </div>
      </div>
    )
  }
}

export default AthleteCard;