import { SELECT_RESULT, UNSELECT_RESULT, UNSET_POI } from '../constants/ActionTypes'

const initialState = {
  viewDetails: false
}

const globeStatus = (state = initialState, action) => {
  switch (action.type) {
    case SELECT_RESULT: {
      return Object.assign({}, state, { viewDetails: action.isAutoPlay === false ? true : false })
      // return Object.assign({}  , state, { viewDetails: true })
    }
    case UNSELECT_RESULT: {
      return Object.assign({}, state, { viewDetails: false })
    }
    case UNSET_POI: {
      return Object.assign({}, state, { viewDetails: false })
    }
    default:
      return state
  }
};

export default globeStatus
