import React, { Component } from 'react'
import Spinner from 'react-spinkit'
import AthleteFilters from './AthleteFilters'
import AthleteCard from './AthleteCard'
import AthleteDetailCard from './AthleteDetailCard'
import './AthleteList.scss'

class AthleteList extends Component {
  constructor(props) {
    super(props)

    this.cardRef = React.createRef()

    this.state = {
      showFilters: false,
      autoPlay: false,
      autoPlayId: null,
      autoPlayInterval: 10000,
      autoRefresh: false,
      daysAgo: 60,
      refreshInterval: 3600000, // one every hour
    }

    this.refreshTimer = null
    this.autoPlayTimer = null

    this.onClickToggleFilters = this.onClickToggleFilters.bind(this)
    this.onClickRefresh = this.onClickRefresh.bind(this)
    this.onClickAutoPlay = this.onClickAutoPlay.bind(this)
    this.triggerAutoScripts = this.triggerAutoScripts.bind(this)
  }

  onClickToggleFilters() {
    return this.setState({ showFilters: !this.state.showFilters })
  }

  onClickAthleteCard(result) {
    const { onSelectResult } = this.props
    const autoPlay = false
    window.clearInterval(this.autoPlayTimer)
    return this.setState({ autoPlay: autoPlay }, onSelectResult(result, autoPlay))
  }

  onCloseDetail() {
    const { onUnselectResult } = this.props
    return onUnselectResult()
  }

  onClickRefresh() {
    const { fetchGraceNoteData } = this.props
    const newDaysAgo = this.state.daysAgo
    this.setState({ daysAgo: newDaysAgo }, () => fetchGraceNoteData({ daysAgo: newDaysAgo }))
  }

  onClickAutoPlay() {
    const _this = this
    this.setState({ autoPlay: !this.state.autoPlay, autoRefresh: !this.state.autoRefresh }, () => _this.triggerAutoScripts())
  }

  focusNextResult() {
    const { phaseResults, onSelectResult } = this.props
    let currentId, currentIndex, nextIndex
    if (this.state.autoPlayId === null) {
      nextIndex = 0
    } else {
      currentId = this.state.autoPlayId
      currentIndex = phaseResults.filtered.findIndex(x => x.n_PhaseResultID === currentId)
      nextIndex = currentIndex < phaseResults.filtered.length - 1 ? currentIndex + 1 : 0
    }
    const nextResult = phaseResults.filtered[nextIndex]
    const domElement = this.cardRef.current.childNodes[nextIndex]
    if (domElement !== undefined) {
      domElement.scrollIntoView({ block: 'center', behavior: 'smooth' })
    }
    return this.setState({ autoPlayId: nextResult.n_PhaseResultID}, () => onSelectResult(nextResult, true))
  }

  triggerAutoScripts() {
    const _this = this

    if (this.state.autoRefresh === true) {
      this.refreshTimer = window.setInterval(() => {
        _this.onClickRefresh()
      }, this.state.refreshInterval)
    } else {
      window.clearInterval(this.refreshTimer)
    }

    if (this.state.autoPlay === true) {
      this.autoPlayTimer = window.setInterval(() => {
        _this.focusNextResult()
      }, this.state.autoPlayInterval)
    } else {
      window.clearInterval(this.autoPlayTimer)
    }
  }

  render() {
    const _this = this
    const { selectedResult, globeStatus, filterOptions, setFilters, phaseResults } = this.props
    const filteredResults = phaseResults.filtered
    const poiResults = phaseResults.poi
    const cardSource = poiResults.length > 0 ? poiResults : filteredResults
    const athleteCards = cardSource.map( r =>{
      let isActive = false
      if (selectedResult !== null) {
        isActive = selectedResult.n_PhaseResultID === r.n_PhaseResultID ? true : false
      }
      return <AthleteCard key={r.n_PhaseResultID} result={r} isActive={isActive} onClick={() => _this.onClickAthleteCard(r)} />
    } )

    const filterCssClass = `float-right btn btn-link ${this.state.showFilters ? '' : 'text-white'}`
    const refreshCssClass = `float-right btn btn-link ${phaseResults.loading ? 'animation--rolling' : 'text-white'}`
    const autoPlayCssClass = `float-right btn btn-link ${this.state.autoPlay ? '' : 'text-white'}`
    const heading = cardSource.length > 0 ? `Found ${cardSource.length} champions over the last ${this.state.daysAgo} days` : 'Fetching data ...'

    /** Spinning loader
    const loader = phaseResults.loading === true ? (
      <div className="d-flex p-3 align-items-center justify-content-center">
        <Spinner name="three-bounce" color="#00bc8c" />
      </div>
      ) : null
    */

    const cssClass = (globeStatus.viewDetails === true && this.state.autoPlay === false) ? 'detail--active' : ''

    return (
      <div className={`athlete-list__container ${cssClass}`}>
        <div className="section__container">
          <div className="section px-3 pt-3 pb-0">
            <button onClick={this.onClickToggleFilters} className={filterCssClass} title="Toggle filters">
              <i className="material-icons md-48">filter_list</i>
            </button>
            <button onClick={this.onClickRefresh} className={refreshCssClass} title="Refresh data">
              <i className="material-icons md-48">refresh</i>
            </button>
            <button onClick={this.onClickAutoPlay} className={autoPlayCssClass} title="Autoplay">
              <i className="material-icons md-48">play_arrow</i>
            </button>
            <h2>Aussie champions</h2>
          </div>

          <div className="section px-3">
            <AthleteFilters filterOptions={filterOptions} setFilters={setFilters} phaseResults={phaseResults} showFilters={this.state.showFilters} />
            <h4 className="my-3 text-muted">{heading}</h4>
          </div>

          <div className="section section--scrollable py-0 px-3" ref={this.cardRef}>
            {athleteCards}
          </div>
        </div>

        <AthleteDetailCard selectedResult={selectedResult} onClick={() => _this.onCloseDetail()} />
      </div>
    );
  }
}

export default AthleteList